import { useState, useEffect } from "react";
import { FileText, X, Upload, Check } from "lucide-react";
import axios from "axios";

const ResultUploadModal = ({
  eventId,
  positionId,
  onClose,
  companyName,
  positionTitle,
  onResultsUpdated,
}) => {
  const [uploadMethod, setUploadMethod] = useState("manual");
  const [students, setStudents] = useState([]);
  const [selectedStudents, setSelectedStudents] = useState(new Set());
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [file, setFile] = useState(null);
  const [csvProcessing, setCsvProcessing] = useState(false);

  useEffect(() => {
    fetchEventStudents();
  }, [eventId, positionId]);

  const fetchEventStudents = async () => {
    try {
      setLoading(true);
      setError(null);

      const queryParams = new URLSearchParams({
        status: "attended",
      });

      if (positionId) {
        queryParams.append("positionId", positionId);
      }

      const response = await axios.get(
        `http://localhost:5000/round-tracking/events/${eventId}/students?${queryParams}`
      );

      if (response.data.success) {
        setStudents(response.data.students || []);

        const alreadyQualified = response.data.students
          .filter((s) => s.result_status === "selected")
          .map((s) => s.id);
        setSelectedStudents(new Set(alreadyQualified));
      }
    } catch (error) {
      console.error("Error fetching students:", error);
      setError(error.response?.data?.message || "Failed to load students");
    } finally {
      setLoading(false);
    }
  };

  const handleStudentToggle = (studentId) => {
    const newSelected = new Set(selectedStudents);
    if (newSelected.has(studentId)) {
      newSelected.delete(studentId);
    } else {
      newSelected.add(studentId);
    }
    setSelectedStudents(newSelected);
  };

  const handleFileUpload = async (event) => {
    const selectedFile = event.target.files[0];
    if (!selectedFile) return;

    setFile(selectedFile);
    setCsvProcessing(true);
    setError(null);

    const reader = new FileReader();
    reader.onload = async (e) => {
      try {
        const text = e.target.result;
        const lines = text.split("\n").filter((line) => line.trim());

        const hasHeader = isNaN(lines[0].split(",")[0].trim());
        const dataLines = hasHeader ? lines.slice(1) : lines;

        const csvData = dataLines
          .map((line) => {
            const values = line.split(",");
            return values[0].trim();
          })
          .filter(Boolean);

        if (csvData.length === 0) {
          setError("No valid data found in CSV file");
          setCsvProcessing(false);
          return;
        }

        const response = await axios.post(
          `http://localhost:5000/round-tracking/events/${eventId}/upload-csv`,
          { csvData }
        );

        if (response.data.success) {
          onResultsUpdated?.();
          onClose();
        }
      } catch (error) {
        console.error("CSV processing error:", error);
        setError(
          error.response?.data?.message ||
            "Failed to process CSV. Please ensure it contains valid registration numbers."
        );
      } finally {
        setCsvProcessing(false);
      }
    };

    reader.onerror = () => {
      setError("Failed to read file");
      setCsvProcessing(false);
    };

    reader.readAsText(selectedFile);
  };

  const handleSubmit = async () => {
    if (uploadMethod === "manual") {
      try {
        setLoading(true);
        setError(null);

        const qualifiedRegistrationNumbers = students
          .filter((student) => selectedStudents.has(student.id))
          .map((student) => student.registration_number);

        if (qualifiedRegistrationNumbers.length === 0) {
          setError("Please select at least one student");
          setLoading(false);
          return;
        }

        const response = await axios.post(
          `http://localhost:5000/round-tracking/events/${eventId}/results`,
          {
            qualifiedRegistrationNumbers,
            method: "manual",
          }
        );

        if (response.data.success) {
          onResultsUpdated?.();
          onClose();
        }
      } catch (error) {
        console.error("Error updating results:", error);
        setError(error.response?.data?.message || "Failed to update results");
      } finally {
        setLoading(false);
      }
    }
  };

  return (
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-2xl max-w-4xl w-full max-h-[90vh] overflow-hidden flex flex-col shadow-2xl">
        {/* Header */}
        <div className="px-8 py-6 border-b border-gray-100">
          <div className="flex items-start justify-between">
            <div>
              <h3 className="text-2xl font-semibold text-gray-900">
                Upload Results
              </h3>
              <p className="text-sm text-gray-500 mt-2">
                {companyName} · {positionTitle}
              </p>
            </div>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-lg p-2 transition-all"
            >
              <X size={20} />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="p-8 overflow-y-auto flex-1">
          {/* Method Selection */}
          <div className="inline-flex rounded-xl bg-gray-100 p-1 mb-8">
            <button
              className={`px-6 py-2.5 rounded-lg text-sm font-medium transition-all ${
                uploadMethod === "manual"
                  ? "bg-white text-gray-900 shadow-sm"
                  : "text-gray-600 hover:text-gray-900"
              }`}
              onClick={() => setUploadMethod("manual")}
            >
              Manual Selection
            </button>
            <button
              className={`px-6 py-2.5 rounded-lg text-sm font-medium transition-all ${
                uploadMethod === "file"
                  ? "bg-white text-gray-900 shadow-sm"
                  : "text-gray-600 hover:text-gray-900"
              }`}
              onClick={() => setUploadMethod("file")}
            >
              CSV Upload
            </button>
          </div>

          {uploadMethod === "manual" ? (
            <div className="space-y-4">
              <div className="flex justify-between items-center mb-4">
                <p className="text-sm text-gray-600">
                  {students.length} students attended
                </p>
                <div className="flex gap-3">
                  <button
                    className="text-sm text-blue-600 hover:text-blue-700 font-medium"
                    onClick={() =>
                      setSelectedStudents(new Set(students.map((s) => s.id)))
                    }
                  >
                    Select All
                  </button>
                  <button
                    className="text-sm text-gray-600 hover:text-gray-700 font-medium"
                    onClick={() => setSelectedStudents(new Set())}
                  >
                    Clear
                  </button>
                </div>
              </div>

              {students.length > 0 ? (
                <div className="border border-gray-200 rounded-xl overflow-hidden">
                  <div className="max-h-96 overflow-y-auto">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50 sticky top-0">
                        <tr>
                          <th className="px-6 py-4 text-left w-12">
                            <input
                              type="checkbox"
                              className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                              checked={
                                students.length > 0 &&
                                selectedStudents.size === students.length
                              }
                              onChange={() => {
                                if (selectedStudents.size === students.length) {
                                  setSelectedStudents(new Set());
                                } else {
                                  setSelectedStudents(
                                    new Set(students.map((s) => s.id))
                                  );
                                }
                              }}
                            />
                          </th>
                          <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Registration
                          </th>
                          <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Name
                          </th>
                          <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Branch
                          </th>
                          <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            CGPA
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-100">
                        {students.map((student) => (
                          <tr
                            key={student.id}
                            className="hover:bg-gray-50 transition-colors cursor-pointer"
                            onClick={() => handleStudentToggle(student.id)}
                          >
                            <td className="px-6 py-4">
                              <input
                                type="checkbox"
                                className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                                checked={selectedStudents.has(student.id)}
                                onChange={() => handleStudentToggle(student.id)}
                                onClick={(e) => e.stopPropagation()}
                              />
                            </td>
                            <td className="px-6 py-4 text-sm font-medium text-gray-900">
                              {student.registration_number}
                            </td>
                            <td className="px-6 py-4 text-sm text-gray-900">
                              {student.name}
                            </td>
                            <td className="px-6 py-4 text-sm text-gray-600">
                              {student.department} - {student.branch}
                            </td>
                            <td className="px-6 py-4 text-sm text-gray-600">
                              {student.cgpa
                                ? parseFloat(student.cgpa).toFixed(2)
                                : "N/A"}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              ) : (
                <div className="text-center py-16 text-gray-400">
                  <FileText className="mx-auto h-12 w-12 mb-3" />
                  <p>No students attended this round yet</p>
                </div>
              )}
            </div>
          ) : (
            <div>
              <div className="border-2 border-dashed border-gray-300 rounded-2xl p-12 text-center hover:border-blue-400 hover:bg-blue-50/50 transition-all">
                <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-blue-100 mb-4">
                  <Upload className="h-8 w-8 text-blue-600" />
                </div>
                <label htmlFor="file-upload" className="cursor-pointer block">
                  <p className="text-base font-medium text-gray-900 mb-2">
                    Upload CSV File
                  </p>
                  <input
                    id="file-upload"
                    name="file-upload"
                    type="file"
                    accept=".csv"
                    className="sr-only"
                    onChange={handleFileUpload}
                    disabled={csvProcessing}
                  />
                  <button
                    type="button"
                    className="mt-3 inline-flex items-center px-6 py-2.5 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700 transition-colors shadow-sm"
                  >
                    {csvProcessing ? "Processing..." : "Choose File"}
                  </button>
                  {file && (
                    <div className="mt-4 inline-flex items-center px-4 py-2 bg-green-50 text-green-700 rounded-lg">
                      <Check size={16} className="mr-2" />
                      <span className="text-sm font-medium">{file.name}</span>
                    </div>
                  )}
                </label>
                <p className="mt-4 text-xs text-gray-500">
                  Format: Registration numbers (one per line or comma-separated)
                </p>
                <p className="text-xs text-gray-400 mt-1">
                  Example: 21BCS001, 21BCS002
                </p>
              </div>
            </div>
          )}
        </div>

        {/* Error Display */}
        {error && (
          <div className="px-8 py-4 bg-red-50 border-t border-red-100">
            <p className="text-sm text-red-600">{error}</p>
          </div>
        )}

        {/* Loading Overlay */}
        {(loading || csvProcessing) && (
          <div className="absolute inset-0 bg-white/80 backdrop-blur-sm flex items-center justify-center z-10">
            <div className="text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-4 border-gray-200 border-t-blue-600 mx-auto"></div>
              <p className="mt-4 text-sm font-medium text-gray-600">
                {csvProcessing ? "Processing CSV..." : "Updating results..."}
              </p>
            </div>
          </div>
        )}

        {/* Footer */}
        <div className="px-8 py-5 bg-gray-50 border-t border-gray-100 flex justify-between items-center">
          <div className="text-sm text-gray-600">
            {uploadMethod === "manual" && (
              <span className="font-medium">
                {selectedStudents.size} selected
              </span>
            )}
          </div>
          <div className="flex items-center gap-3">
            <button
              className="px-5 py-2.5 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-100 transition-colors"
              onClick={onClose}
              disabled={loading || csvProcessing}
            >
              Cancel
            </button>
            {uploadMethod === "manual" && (
              <button
                className="px-5 py-2.5 bg-blue-600 rounded-lg text-sm font-medium text-white hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed shadow-sm"
                onClick={handleSubmit}
                disabled={loading || selectedStudents.size === 0}
              >
                Update Results
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ResultUploadModal;
